<template>
    <div class="container">
        <div class="row">
            <div class="col-xs-12 col-sm-12 col-md-8 col-lg-8">
                <v-card elevation="4" width="500" class="mx-auto justify-md-center float-right">
                    <v-toolbar dark class="nav-color">
                        <v-toolbar-title class="title-nav-bar-text">
                            ¡Bienvenido!
                        </v-toolbar-title>
                    </v-toolbar>
                    <ValidationObserver v-slot="{ validate }" ref="form">
                        <form @submit.prevent="validateForm(validate)">
                            <v-card-text>
                                <ValidationProvider name="Usuario" rules="required" v-slot="{ errors }">
                                    <v-text-field label="Usuario" color="orange" v-model="usuario.userName" outlined
                                        clearable dense :error-messages="errors"></v-text-field>
                                </ValidationProvider>
                                <ValidationProvider name="Password" rules="required" v-slot="{ errors }">
                                    <v-text-field label="Password" color="orange" v-model="usuario.password" outlined
                                        clearable dense :error-messages="errors" type="password"></v-text-field>
                                </ValidationProvider>
                            </v-card-text>
                            <v-card-actions class="justify-content-between">
                                <v-btn outlined class="ma-1 pa-4" color="orange" type="submit">
                                    Login
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </div>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import { extend, ValidationProvider, ValidationObserver } from 'vee-validate';
import { required } from 'vee-validate/dist/rules';

extend('required', {
    ...required,
    message: 'El campo {_field_} es obligatorio',
})

export default {
    name: 'LoginComponent',
    components: {
        ValidationProvider,
        ValidationObserver
    },
    data() {
        return {
            usuario: {
                userName: null,
                password: null,
            }
        }
    },
    methods: {
        ...mapMutations(['setUserName', 'setPassword']),

        validateUser() {
            this.$store.commit('setUserName', this.usuario.userName);
            this.$store.commit('setPassword', this.usuario.password);
        },
        redirectToSaludo() {
            this.$router.push("/saludo");
        },
        validateForm(validate) {
            validate().then(isValid => {
                if (isValid) {
                    this.validateUser();
                    this.redirectToSaludo();
                }
            });
        },

    },
}
</script>

<style>
.nav-color {
    background-color: orange !important;
}

.title-nav {
    color: white !important;
    font-family: "Roboto", "sans-serif";
    font-weight: 400;
}

.title-nav-bar-text {
    font-family: "Roboto", "sans-serif";
    font-weight: 300;
}
</style>